<template>
    <div class="deposit" id="deposit">
        <section v-for="(item,inedx) in depositData" :class="{Invalid:item.isRefund==0}" :key="inedx">
            <h3>入住押金
                <div class="fr" v-if="item.isRefund==0" @click="deletedepositfn(item)">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-weibiaoti--"></use>
                    </svg>
                </div>
            </h3>
            <div class="content">
                <div class="deposit-top">
                    <p>押金金额</p>
                    <span class="red">￥{{item.deposit|money}}</span>
                </div>
                <div class="deposit-bottom">
                    <p>时间日期
                        <span class="fr">{{item.payTime|DateFormat("yyyy-MM-dd hh:mm")}}</span>
                    </p>
                    <p>交易类型
                        <span class="fr">入住押金</span>
                    </p>
                    <p class="explain" v-if="item.checkOutInstructions">退还说明： {{item.checkOutInstructions}}</p>
                </div>
            </div>
        </section>
    </div>
</template>

<script type='text/ecmascript-6'>
import { Personal } from "@/assets/js/request.js";
export default {
  data() {
    return {
      hotelId: this.$store.state.hotelId,
      depositData: [],
      page: 1,
      pages: 1,
      flag: true
    };
  },
  mounted() {
    var _this = this;
    _this.getdepositfn();
    //获取滚动条当前的位置
    function getScrollTop() {
      var scrollTop = 0;
      if (document.documentElement && document.documentElement.scrollTop) {
        scrollTop = document.documentElement.scrollTop;
      } else if (document.body) {
        scrollTop = document.body.scrollTop;
      }
      return scrollTop;
    }

    //获取当前可是范围的高度
    function getClientHeight() {
      var clientHeight = 0;
      if (document.body.clientHeight && document.documentElement.clientHeight) {
        clientHeight = Math.min(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      } else {
        clientHeight = Math.max(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      }
      return clientHeight;
    }

    //获取文档完整的高度
    function getScrollHeight() {
      return Math.max(
        document.body.scrollHeight,
        document.documentElement.scrollHeight
      );
    }
    window.onscroll = function() {
      if (getScrollTop() + getClientHeight() > getScrollHeight() - 50) {
        _this.page++;
        if (_this.page > _this.pages) {
          _this.Toast("已加载所有数据");
          return false;
        }
        _this.getdepositfn();
      }
    };
  },
  methods: {
    getdepositfn() {
      if (this.flag) {
        this.flag = false;
        Personal.getdeposit({
          Suffix: this.hotelId + "/deposit",
          params: {
            hotelId: this.hotelId,
            page: this.page
          },
          fn: data => {
            this.flag = true;
            this.pages = data.pages;
            this.depositData = this.depositData.concat(data.records);
          }
        });
      }
    },
    deletedepositfn(item) {
      Personal.deletedeposit({
        Suffix: this.hotelId + "/deposit/" + item.orderId,
        fn: data => {
          this.depositData.splice(this.depositData.indexOf(item), 1);
          this.Toast("删除成功");
        }
      });
    }
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>

</style>



